<template>
   <div>
       <div style="width: 100px;margin-bottom: 10px;"><a-button @click="handleAdd"></a-button></div>
       <a-table :columns="columns" :dataSource="data" bordered>
           <!---->
           <template slot="action" slot-scope="text, record">
               <a @click="handleEditor(record)"></a>
               <a-divider type="vertical" />
               <a @click="handleDelete(record.id)"></a>
           </template>
       </a-table>
       <!---->
       <a-modal :title="modifyTitle" v-model="visible" @ok="handleOk" >
           <div class="smallTip"></div>
           <div style="display: flex;">
               <div class="left" style="flex: 1;">
                   <div><span class="tip"></span><a-input v-model="ediItem.startDes"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.arriveDes"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.company"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.companyType"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.airtype"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.price"/></div>
               </div>
               <div class="middle" style="flex: 1;margin:0 15px;">
                   <div><span class="tip"></span><a-input v-model="ediItem.startDate"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.startAirport"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.arriveDate"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.arriveAirport"/></div>
                   <div><span class="tip"></span><a-input v-model="ediItem.duration"/></div>
               </div>
           </div>
       </a-modal>
   </div>
</template>
<script>
    const columns = [
        {title: 'id', dataIndex: 'id'},
        {title: '', dataIndex: 'startDes'},
        {title: '', dataIndex: 'arriveDes'},
        {title: '', dataIndex: 'company'},
        {title: '', dataIndex: 'companyType'},
        {title: '', dataIndex: 'airtype'},
        {title: '', dataIndex: 'price',defaultSortOrder: 'descend', sorter: (a, b) => a.price - b.price,},
        {title: '', dataIndex: 'startDate'},
        {title: '', dataIndex: 'startAirport'},
        {title: '', dataIndex: 'arriveDate'},
        {title: '', dataIndex: 'arriveAirport'},
        {title: '', dataIndex: 'duration'},
        {
            title: '',
            key: 'operation',
            scopedSlots: { customRender: 'action' },
        },
    ];
    export default {
        data() {
            return {
                columns,
                data:[],
                //
                ediItem:{},
                visible:false,//
                modifyTitle:'',//
            };
        },
        methods: {
            /**/
            handleAdd(){
                this.visible = true;
                this.modifyTitle = "";
                this.ediItem = {startDes:"",arriveDes:"",company:"",companyType:"",startDate:"",startAirport:"",
                    arriveDate:"", arriveAirport:"",duration:"",airtype:"",price:""}
            },
            /**/
            handleEditor(item){
                this.visible = true;
                this.ediItem = item;
                this.modifyTitle = "";
            },
            /**/
            handleOk(){
                var item = this.ediItem;
                if(item.startDes==""||item.arriveDes==""||item.company==""||item.companyType==""||item.startDate==""||item.price==""||
                    item.arriveDate==""||item.arriveAirport==""||item.duration==""||item.airtype==""||item.startAirport==""){
                    this.$message.error("");
                }else {
                    /**/
                    if(this.modifyTitle == ''){
                        this.$https.post('ticket/modify',item).then( res => {
                            if(res.body.success){
                                this.$message.success(res.body.rspDesc);
                                this.updatePage();
                            }
                        })
                    }else{//
                        this.$https.post('ticket/add',item).then( res => {
                            if(res.body.success){
                                this.$message.success(res.body.rspDesc);
                                this.updatePage();
                            }
                        })
                    }

                    this.visible = false;
                }

            },
            /**/
            handleDelete(id){
                var _this=this;
                this.$confirm({
                    title: '?',
                    okText: '',
                    okType: 'danger',
                    cancelText: '',
                    onOk() {
                        _this.$https.post('ticket/delete',{id:id}).then( res => {
                            if(res.body.success){
                                _this.$message.success(res.body.rspDesc);
                                _this.updatePage();
                            }
                        })
                    },
                });
            },
            /**/
            updatePage(){
                this.$https.post('ticket/getAll').then( res => {
                    this.data = res.body.TicketList;
                })
            },
        },
        created(){
            this.updatePage();
        }
    };
</script>


<style scoped>

</style>