<template>
    <div class="user">
        <a-table bordered :dataSource="dataSource" :columns="columns">
            <!---->
            <template slot="picture" slot-scope="text,record">
                <img :src="record.picture" alt="" style="width: 80px;">
            </template>
            <!---->
            <template slot="operation" slot-scope="text, record">
                <a-popconfirm
                        v-if="dataSource.length"
                        title="Sure to delete?"
                        @confirm="() => onDelete(record.id)"
                >
                       <a href="javascript:;"></a>
                </a-popconfirm>
            </template>
        </a-table>
    </div>
    
</template>

<script>
    export default {
        name: "UserAdmin",
        data(){
            return{
                /*---*/
                dataSource: [],
                count: 2,
                /**/
                columns: [
                    { title: 'id',dataIndex: 'id',key:'id'},
                    { title: '',dataIndex: 'type',key:'type'},
                    { title: '',dataIndex: 'nickName',key:'nickName',},
                    { title: '',dataIndex: 'account',key:'account',},
                    { title: '',dataIndex: 'password',key:'password',},
                    { title: '',dataIndex: 'passQues',key:'passQues',},
                    { title: '',dataIndex: 'passAns',key:'passAns',},
                    { title: '',dataIndex: 'picture',key:'picture', scopedSlots: { customRender: 'picture' },},
                    { title: '',dataIndex: 'sex',key:'sex',},
                    { title: '',dataIndex: 'des',key:'des',},
                    { title: '',dataIndex: 'address',key:'address',},
                    { title: '', dataIndex: 'operation',key:'operation', scopedSlots: { customRender: 'operation' },},
                ],
            };
        },
        methods:{
            /**/
            onDelete(id) {
                this.$https.post('user/delete',{id:id}).then( res => {
                    if(res.body.success){
                        this.$message.success(res.body.rspDesc);
                        this.updaataPage();
                    }
                })
            },
            /**/
            updaataPage(){
                this.$https.post('user/getall').then( res => {
                    this.dataSource = res.body.userList;
                    /**/
                    this.dataSource.forEach(item=>{
                        if(item.passQues == 'address'){
                            item.passQues = ""
                        }else if(item.passQues == 'bir'){
                            item.passQues = ""
                        }else if(item.passQues == 'school'){
                            item.passQues = ""
                        }else{
                            item.passQues = ""
                        }
                        item.type = item.type == '1'?"":"";
                        item.sex = item.sex == '1'?"":"";
                    })
                    //
                    this.count = this.dataSource.length;
                })
            },
        },
        created(){
            this.updaataPage()
        }
        
    }
</script>
